div {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.fstyle {
    margin: auto;
    display: inline-block;
    border: 1px solid #ccc;
    padding: 10px;
    background-color: white;
    box-shadow: 0 0 8px #888888;
}
.dstyle {
    text-align: center;
    margin: 40px;
}
.recipes {
    padding-top: 15px;
    padding-right: 30px;
    padding-left: 30px;
    font-family: Verdana, Geneva, sans-serif;
}

ol {
    font-family: Verdana, Geneva, sans-serif;
	font-size: 13px;
}

.bowl-title {
    text-align: center;
    padding: 10px 10px;
}

.sub-title {
    color: tomato;
    padding-top: 15px;
}

.video-container {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
}

.video-container iframe, .video-container object, .video-container embed {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
}
    
#map {
  height: 91vh;
  width: 100vw;
  position: fixed;
}
.leaflet-routing-container h2{
  font-size: 0.8rem !important;
  padding-top: 0 !important;
  padding-right: 0 !important;
  padding-left: 0 !important;
}
.leaflet-routing-container h3{
  font-size: 0.8rem !important;
  text-align: right !important;
  padding-top: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.leaflet-routing-alt, .leaflet-routing-geocoders, .leaflet-routing-error {
  padding: 6px;
  margin-top: 2px;
  border-bottom: 1px solid #ccc;
  max-width: 70vw;
  max-height: 200px;
  overflow-y: auto;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.leaflet-routing-container-hide .leaflet-routing-collapse-btn {
  position: absolute;
  left: 5.5px;
  top: 0.7vh;
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  cursor: pointer;
}
.leaflet-routing-container-hide:hover {
  background-color: #F4F4F4;
}
.leaflet-reset-button {
  position: relative;
  width: 32px;
  height: 32px;
  display: block;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  padding: 3px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.65);
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAQAAAD8x0bcAAAAmklEQVR4AcXQJUIEQBQA0IdTqXAWvFKxSoVIRxPuegsqGSm4pb0BTkI/ZV3jvonzXfU0GZMQQsKoJszK0+pBOLRi0oHwYFnIUePYk14p3b5EftCIMChjXRQGnbqWMS+KBX3Yl9KsK/3ygvZUdOJGvnFDhYMPyNYnDMtR6yTnBB1enaiXp82dcGjNlnPhRosimoxKCK8ujKtXLf9y7Taq69vEwgAAAABJRU5ErkJggg==);
  background-size: 18px 18px;
  background-color: white;
  cursor: pointer;
}
.leaflet-reset-button:hover {
  background-color: #F4F4F4;
}
h3 {
    text-align: center;
	padding-top: 20px;
	padding-left: 10px;
	padding-right: 10px;
}

h2 {
	padding-top: 15px;
    padding-right: 30px;
    padding-left: 30px;
    font-family: Verdana, Geneva, sans-serif;
	font-size: 14px;
	line-height: 1.6;
}

p {
    font-family: Verdana, Geneva, sans-serif;
	font-size: 13px;
}

.image {
	max-width: 100%;
	height: auto;
	padding-top: 10px;
	padding-bottom: 10px;
}

.container {
    padding-top: 10px;
	padding-bottom: 10px;
	position: relative;
	max-width: 100%;
	height: auto;
    text-align: center;
    color: black;
	font-family: Verdana, Geneva, sans-serif;
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.container .btn {
	position: absolute;
    top: 100%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: #555;
    color: white;
    font-size: 16px;
    padding: 12px 24px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
	font-family: Verdana, Geneva, sans-serif;
}

.container .btn:hover {
    background-color: tomato;
}
